/* header 영역 css */
div > ul.header-ui { background-color: aquamarine;}
div > ul.header-ui > li { list-style-type:none; display: inline; margin-left: 2rem;}
div > ul.header-ui > li.on{ background-color: beige; color: brown; }

/* blog info 영역 css */
div > div.blog-info > dl { background-color: grey; }
div > div.blog-info > dl > dd { font-weight: 700; color: rgb(110, 103, 40); display: inline; margin-left: 2rem; }
div > div.blog-info > dd > dd.on { background-color: lawngreen; font-weight: 1000; color: lemonchiffon; }
div > div.blog-connenct > span { display: none; overflow: hidden; }
div > div.blog-connenct > span.over { display: inline-block; overflow: scroll; }

/* div 영역잡고 선 스타일 내기 
border: 선굵기, style, color
border-radius : 외곽 선 둥근 모서리
*/
div > div.profile-info > div.profile-about-me.on > dl > dd > p > code {font-size: 10px; border-style: groove; border-color: dodgerblue;}
div > div.profile-info > div.profile-about-me.on > dl > dd > div {
    border: 1px outset gold; border-radius: 10px;
    -moz-border-radius: 10px; -webkit-border-radius: 10px;
}
/*RESET*/
* {
    margin: 0;
    padding: 0;
}

body {
    font: 17px 'Nanum Gothic', sans-serif;
}

a {
    text-decoration: none;
    color: #404040;
}

li {
    list-style: none;
}

/*BODY*/

/*팝업창*/
#popup {
    display: none; /*숨기기*/
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.9);
}

#popup.on {
    display: block; /*숨기기*/
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.9);
}
#popmenu {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 500px;
    height: 300px;
    text-align: center;
    background: #fff;
}

#popmenu p {
    margin-top: 80px;
}

.exit {
    position: absolute;
    left: 50%;
    bottom: 10px;
    transform: translate(-50%,0);
    width: 60px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    background: #007AAE;
    cursor: pointer;
}

/*본문내용*/
#contents {
    width: 300px;
    height: 150px;
    text-align: center;
    line-height: 150px;
    color: #fff;
    background: #555;
    cursor: pointer;
}

a:hover{
    text-decoration:'none';
    color : 'black';
}

.MuiPaper-rounded.on{
    border: 3px solid red;
    box-shadow: 10px 10px 10px gray;
    margin: 10px;margin-bottom: 10px;
}

dl { width:100%; overflow:hidden; background:#ff0; } 
dt { float:left; width:10%; /* adjust the width; make sure the total of both is 100% */ background:#007AAE; } 
dd { float:left; width:90%; /* adjust the width; make sure the total of both is 100% */ background:#fff }